<template>
    <div class="left-delete"> 
        <div class="move"
        @touchstart="_touchStart"
        @touchmove="_touchMove"
        @touchend="_touchEnd"
        :style="txtStyle">
        <slot></slot>
    </div>
    <div class="deleteIcon" :style="zIndex"  @click.prevent="deleteItem(index)"></div>
</div>
</template>

<script>
export default {
    props: {
        index: Number
    },
    data() {
        return {
                startX: 0,//
                moveX: 0,//
                disX: 0,//
                txtStyle: '',
                delWidth: 30,
                top: '',
                zIndex: 'z-index:-1',
            }
        },
        methods: { 
            _touchStart: function(ev) {  
                ev = ev || event;
                if(ev.touches.length == 1){
                    // 
                    this.startX = ev.touches[0].clientX;
                    // console.log(this.startX)
                }
            },
            _touchMove: function(ev) {
                ev = ev || event;
                if(ev.touches.length == 1) {
                    // 
                    this.moveX = ev.touches[0].clientX
                    // 
                    this.disX = this.startX - this.moveX;
                    // console.log('disX==>',this.disX)
                    // 
                    if(this.disX < 0 || this.disX == 0) {
                        // console.log('');
                        this.txtStyle = "transform:translateX(0)";
                    }else if (this.disX > 0) {// -leftdelWidth delWidth
                        this.txtStyle = "transform:translateX(-" + this.disX + "%)";
                        if (this.disX >= this.delWidth) {
                            this.txtStyle = "transform:translateX(-" + this.delWidth + "%)"; 
                        }
                    }
                }
            },
            _touchEnd: function(ev) {
                if (event.changedTouches.length == 1) {  
                    // console.log(event.changedTouches[0].clientX)
                    // 
                    let endX = event.changedTouches[0].clientX;
                    // ,
                    this.disX = this.startX - endX;
                    //1/2
                    if(this.disX < this.delWidth/2){
                        this.txtStyle = "transform:translateX(0)";
                    }else{
                        this.txtStyle = "transform:translateX(-" + this.delWidth + "%)"; 
                    }
                    
                }
            },
            deleteItem: function(index) { 
                this.$emit('deleteItem', index); 
            }
        }
    }
    </script>

    <style>
    .left-delete{
        width:100%; 
        height:0;
        padding-bottom: 30%;
        background: #ccc;
        position:relative;
        z-index:2;
    }
    .move{
        position: relative;
        width: 100%;
        height:0;
        padding-bottom: 30%; 
        transition: all .5s ease-in-out;
    }
    .deleteIcon{
        width: 30%;
        height:0;
        padding-bottom: 30%;
        background: url(../../assets/svg/junk-box.svg) no-repeat center center #d9534f;
        text-align: center; 
        position: absolute;
        right:0;
        top:0;  
    }
    </style>


