<template> 
  <div class="index-content">
    <div class="container book-tag-box">

      <div class="row type-list" v-for = "item in tagData">
        <div class="col-xs-12 col-md-12 title"><h2 v-text="item.title"></h2></div> 
        <div class="col-xs-4 col-md-4 tag-a" v-for = "name in item.tag"> 
          <router-link :to="{path:'/BookList/'+name}" class="text-center emit excessive" v-text = "name"></router-link> 
        </div> 
      </div>  

    </div>
  </div> 
</template>

<script> 


export default { 
  data:function(){
    return {
      tagData: [{"title":"","tag":["","","","","","","","","","","","","","","","","","","","","","","","","","",""]},{"title":" ","tag":["","","","","","","","","","","","","","","","","","","","","","","","","","","","","","J.K.","","","","","",""]},{"title":"","tag":["","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","",""]},{"title":"","tag":["","","","","","","","","","","","","","","","","","","","",""]},{"title":"","tag":["","","","","","","","","","","","",""]},{"title":"","tag":["","","","","","","","web","","UE","","","UCD","",""]}],
    }
  },
  created:function(){  
  } 
}



</script>




<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.book-tag-box .title h2 {
  font-size: 18px;
  font-weight: 900;
  position: relative;
}

.book-tag-box .title h2:before {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background: #111;
  position: absolute;
  left: 0;
  bottom: -4px;
}

.book-tag-box .title h2:after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: #111;
  position: absolute;
  left: 0;
  bottom: -7px;
}

.book-tag-box .type-list {
  background: #fff;
}

.book-tag-box .type-list .tag-a {
  padding: 0 10px;
  margin: 10px 0;
}

.book-tag-box .type-list .tag-a a {
  display: block;
  padding: 8px;
  background: #e7e7e7;
  border: 1px solid #ccc;
  font-size: 16px;
  color: #111;
  text-decoration: none;
}

.book-tag-box .type-list .tag-a a:active {
  background: #f04646;
  color: #fff;
}

.book-tag-box .type-list .tag-a a:hover {
  text-decoration: none;
}
</style>



